export default {
	data() {
		return {
			userInfo:{
				phoneNum:'',
				password:'',
				repassword:''
			},
			checked:false,
			inviteCode:'',
			smscode:'',
			codeBtnShow:true,
			codeTimer:null,
			count:''
		};
	},
	methods:{
		getCode(){
			if(this.codeBtnShow){
				if(this.userInfo.phoneNum == ''){
					uni.showToast({
						title:'手机号不能为空',
						icon:'none'
					})
					return false;
				}
				this.$uniRequest(
					this.$baseUrl+'/v1/sms/send',
					{
						mobile:this.userInfo.phoneNum,
						smstype:2
					},
					(res)=>{
						if(res.data.code == 200){
							uni.showToast({
								title:res.data.msg,
								success: () => {
									this.codeBtnShow = false;
									const TIME_COUNT = 60;
									if (!this.codeTimer) {    
										this.count = TIME_COUNT;    
										this.codeBtnShow = false;    
										this.codeTimer = setInterval(() => {    
											if (this.count > 0 && this.count <= TIME_COUNT) {     
												this.count--;     
											} else {     
												this.codeBtnShow = true;     
												clearInterval(this.codeTimer);     
												this.codeTimer = null;     
											}    
										}, 1000)    
									}  
								}
							})
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none'
							})
						}
					}
				)
			}
			
		},
		confirm(){
			uni.showLoading({
				title:'重置密码中',
				mask:true
			})
			this.$uniRequest(
				this.$baseUrl+'/v1/login/resetPassword',
				{
					mobile:this.userInfo.phoneNum,
					member_pass:this.userInfo.password,
					smscode:this.smscode
				},
				(res)=>{
					if(res.data.code == 200){
						uni.showToast({
							title:'密码重置成功',
							mask:true,
							duration:1000,
							success: () => {
								setTimeout(()=>{
									uni.navigateBack()
								},1000)
							}
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}
			)
		}
	}
}
